.cardItemContainer {
  margin-bottom: 35px;
  display:flex;
  width: 100%;
}

.text {
  display: flex;
  flex-direction: column;
  align-items: start;
  padding-left: 10px;
}
.imgWrapper {
  display: inline-block;
}
.img {
  width: 35px;
  height: 35px;
  display: block;
  border-radius: 50%;
  overflow: hidden;
}

.title {
  font-size: 14px;
  font-weight: 700;
  line-height: 1.3;
  text-decoration: none;
  color: #333333;
  &:hover {
    color: var(--color-primary-base);
    transition: .4s;
  }
}

.subTitle {
  font-size: 12px;
  font-weight: 400;
  color: #666666;
  text-transform: capitalize;
  text-decoration: none;

  &:hover {
    color: var(--color-primary-base);
    transition: .4s;
  }
}

.likeBtn {
  border: none;
  outline: none;
  padding: 0;
  box-shadow: none;
  background-color: transparent;
  margin-left: auto;
  cursor: pointer;
}